(function( $, doc ) {
    "use strict";

    (function(){

        // ACTION minimize
        $(".psource-can--hide").ready(function(){

            var $this = $(this),
                $button = $this.find(".psource-box-header .psource-box--action");

            $button.on("click", function(){

                var $parent = $(this).closest(".psource-can--hide");

                $parent.toggleClass("psource-is--hidden");

            });

        });

        // NEW QUIZ
        // ACTION open/close answer info
        $(".psource-wizard--answer").ready(function(){

            var $this = $(this),
                $button = $this.find(".psource-answer--header .psource-button-action");

            $button.on("click", function(){

                var $parent = $(this).closest(".psource-wizard--answer");

                $parent.toggleClass("psource-is_closed");

            });

        });

    }());
    
}( jQuery, document ));