(function( $, doc ) {
    "use strict";

    (function(){

        $(document).ready(function() {

            // SELECT2 psource-ui
            $(".psource-select").select2({
                allowClear: false,
                minimumResultsForSearch: Infinity,
                containerCssClass: "psource-select2",
                dropdownCssClass: "psource-select2-dropdown"
            });

            // SELECT2 powerform-ui
            $(".psource-form-field--select").select2({
                allowClear: false,
                minimumResultsForSearch: Infinity,
                containerCssClass: "psource-form-field--select2",
                dropdownCssClass: "psource-form-field--select2-dropdown"
            });
            
        });

    }());

}( jQuery, document ));