(function( $, doc ) {
    "use strict";

    (function(){

        // OPEN Modal
        $(document).on("click", ".psource-open-modal", function(e){

            e.preventDefault();
            e.stopPropagation();
            
            var $this = $(this),
                $modal_id = $this.data('modal'),
                $modal = $("#powerform-modal-" + $modal_id),
                $content = $modal.find(".psource-box-modal")
            ;

            $modal.addClass("psource-modal-active");
            $("body").addClass("psource-modal-is_active");

            setTimeout(function(){
                $content.addClass("psource-show");
            });
            
        });
        
        // CLOSE Modal
        $(document).on("click", ".psource-i_close", function(e){

            e.preventDefault();
            e.stopPropagation();

			var $modal = $(this).closest('.psource-modal'),
				$content = $modal.find(".psource-box-modal");

            $content.removeClass('psource-show').addClass('psource-hide');

            setTimeout(function(){
                $modal.removeClass('psource-modal-active');
                $('body').removeClass('psource-modal-is_active');
                $content.removeClass('psource-hide');
            }, 1000);
            
        });

        // Custom Forms
        // WRAP every three layouts
        $("#powerform-modal-dashboard-popup-custom-form").ready(function(){
            
            var $this = $("#powerform-modal-dashboard-popup-custom-form"),
                $box = $this.find(".powerform-box-type");

            for(var i = 0; i < $box.length; i+=3) {
                $box.slice(i, i+3).wrapAll("<div class='powerform-row'></div>");
            }
            
        });

    }());

}( jQuery, document ));